/* src/components/BaseModal.css - Optimized */
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

/* Bi·∫øn t√πy ch·ªânh (Custom Properties) n√™n ƒë∆∞·ª£c ƒë·ªãnh nghƒ©a ·ªü ƒë√¢y ho·∫∑c trong m·ªôt file g·ªëc */
/* Vd: :root { --border-color: #ccc; --border-radius: 4px; --success: #10b981; --font-sm: 0.875rem; } */

.modal-overlay {
  position: fixed;
  inset: 0; /* T·ªëi ∆∞u: Ng·∫Øn g·ªçn thay cho top/right/bottom/left: 0 */
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px;
  z-index: 1000;
  animation: fadeIn 0.25s forwards;
}

.modal {
  background: #fff;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  max-width: 50%;
  height: 80%;
  width: 100%; /* ƒê·∫£m b·∫£o n√≥ gi√£n ra trong max-width */
  position: relative;
  box-shadow: 0 5px 20px rgba(0,0,0,0.2);
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
  animation: slideIn 0.25s forwards;
}

.modal-header {
  padding: 16px;
  border-bottom: 1px solid #e5e7eb;
  background: #f9fafb;
  flex-shrink: 0; /* Gi·ªØ nguy√™n, r·∫•t t·ªët */
}

.modal-title {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: #111827;
}

.form-control {
  width: 100%; /* Th√™m width: 100% ƒë·ªÉ ƒë·∫£m b·∫£o n√≥ l·∫•p ƒë·∫ßy container */
  padding: 0.625rem 0.75rem;
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  background: #fff;
  font-size: var(--font-sm);
  box-sizing: border-box; /* T·ªëi ∆∞u: ƒê·∫£m b·∫£o padding kh√¥ng l√†m tƒÉng t·ªïng chi·ªÅu r·ªông */
}

.modal-content {
  padding: 16px; 
  display: flex;
  flex-grow: 1; /* T·ªëi ∆∞u: Thay height: 100% (c√≥ th·ªÉ g√¢y l·ªói khi flex) b·∫±ng flex-grow: 1 */
  flex-direction: column;
  gap: 12px;
  overflow-y: auto;
}

.modal-row {
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
}

.modal-row input[type="checkbox"] {
  margin-right: 8px;
  /* T·ªëi ∆∞u: Gi·∫£m k√≠ch th∆∞·ªõc v√† lo·∫°i b·ªè padding, background-color b·∫•t th∆∞·ªùng */
  transform: scale(1); /* B·ªè scale(1.2) n·∫øu kh√¥ng c·∫ßn ph√≥ng to */
  cursor: pointer;
  /* B·ªè border-radius: 25px; v√¨ checkbox th∆∞·ªùng l√† h√¨nh vu√¥ng/tr√≤n nh·ªè */
  height: 16px;
  width: 16px;
  /* background-color: #854d0e; ‚ùå B·ªè, d√πng accent-color khi checked */
  /* padding: 20px; ‚ùå B·ªè, khi·∫øn checkbox b·ªã bi·∫øn d·∫°ng */
}

.modal-row input[type="checkbox"]:checked {
  accent-color: var(--success, #10b981); /* S·ª≠ d·ª•ng gi√° tr·ªã d·ª± ph√≤ng n·∫øu --success kh√¥ng t·ªìn t·∫°i */
}

.modal-label { 
  font-weight: 500; 
  color: #333; 
  width: 15%;
  flex-shrink: 0; /* T·ªëi ∆∞u: NgƒÉn label b·ªã co l·∫°i */
}
.modal-value { 
  color: #555; 
  flex-grow: 1; /* T·ªëi ∆∞u: Cho ph√©p value gi√£n ra h·∫øt kh√¥ng gian c√≤n l·∫°i */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-width: 20%;
}
#address { width: 50%;} /* N·∫øu #address l√† m·ªôt .modal-value, n√™n d√πng selector m·∫°nh h∆°n */

.modal-actions {
  padding: 12px 16px;
  border-top: 1px solid #e5e7eb;
  display: flex;
  justify-content: space-between;
  gap: 10px;
  flex-shrink: 0;
  background: #f9fafb;
}

.modal-actions-button {
  display: flex;
  gap: 8px;
  justify-content: center;
  align-items: center;
}

.modal-close {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 32px;
  height: 32px;
  font-size: 22px;
  background: rgba(255,255,255,0.9);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s;
  z-index: 1001;
}
.modal-close:hover { background: rgba(255,255,255,1); }

/* Scrollbar T√πy ch·ªânh (Ch·ªâ hi·ªÉn th·ªã n·∫øu b·∫°n KH√îNG d√πng display: none ·ªü tr√™n) */
.modal-content::-webkit-scrollbar { width: 6px; }
.modal-content::-webkit-scrollbar-thumb { background-color: rgba(0,0,0,0.2); border-radius: 3px; }

/* Alerts */
.alert { padding: 8px 12px; border-radius: 4px; font-weight: 500; margin-bottom: 8px; }
.alert-error { background: #fee2e2; color: #b91c1c; }
.alert-info { background: #e0f2fe; color: #0369a1; }
.alert-warning { background: #fef9c3; color: #854d0e; }

/* Animations */
@keyframes fadeIn { from {opacity:0;} to {opacity:1;} }
@keyframes slideIn { from {transform: translateY(-20px);opacity:0;} to {transform: translateY(0);opacity:1;} }

/* Responsive */
@media(max-width:768px){
  .modal { max-width: 95%; max-height: 95vh; border-radius: 6px; }
  .modal-row { flex-direction: column; align-items: flex-start; gap: 6px; }
  .modal-label, .modal-value { width: 100%; font-size: 0.85rem; }
  .modal-label { flex-shrink: unset; } /* T·ªëi ∆∞u: B·ªè flex-shrink: 0 tr√™n mobile */
}
@media(max-width:480px){
  .modal-actions { flex-direction: column; gap: 8px; }
  .modal-actions button { width: 100%; }
}